export default HireReactDevelopers;
export const queryData = graphql`
  query HireReactDevelopersPageQuery {
    HireReactDevelopersImg: file(
      relativePath: { eq: "hire-react-developers.png" }
    ) {
      childImageSharp {
        gatsbyImageData
      }
    }
    heroImage: file(
      relativePath: { eq: "hirescreens/devPages/react-developers-hero.webp" }
    ) {
      childImageSharp {
        gatsbyImageData(
          layout: CONSTRAINED
          width: 1200
          quality: 90
          placeholder: BLURRED
        )
      }
    }

    site {
      siteMetadata {
        firestore {
          apiKey
          appId
          authDomain
          messagingSenderId
          projectId
          storageBucket
        }
      }
    }

    allMetaJson(filter: { slug: { eq: "hire-react-developers" } }) {
      nodes {
        metaTitle
        metaKeyword
        title
        slug
        description
      }
    }
  }
`;
